<template>
  <div class="container ">
    <table class="table ">
      <ul class=" row mb-6 container">
        <input v-if="editMode" class="input-group-prepend" type="text" v-model="producto.nombre">
        <li class="col-md-2 bg-success" v-else>{{producto.nombre}}</li>
        <input v-if="editMode" class="input-group-prepend"  type="text" v-model="producto.marca">
        <li class="col-md-2 bg-success" v-else>{{producto.marca}}</li>
        <input v-if="editMode" class="input-group-prepend" type="number" v-model="producto.cantidad">
        <li class="col-md-2 bg-success" v-else>{{producto.cantidad}}</li>
      
          <div class=" float-right ">
            <button v-if="editMode" class="btn btn-success " @click="guardarCambios()" >
                Guardar cambios
            </button>
            <button v-else class="btn btn-info" @click="editar()">
                Editar
            </button>

            <button class="btn btn-danger" @click="borrar()">
                Eliminar
            </button>
          </div> 
          </ul>     
    </table>
  </div>
</template>
<script>

export default {
  name:'listaproducto'
  ,
  props:['producto'],
data() {
  return {
    editMode: false,
  }
 },
 methods:{
   borrar(){
     this.$emit('delete')
   },
   editar(){
     this.editMode=true;
   },
   guardarCambios(){
     this.editMode=false;
     this.$emit('update',this.producto)
   }
 }
}
</script>
<style lang="scss" scoped>
ul{
  list-style: none
}

.panel-footer{
  margin-left: 0%;;
}
</style>
