<template>
  <div class="home">
    <h1>TIENDA</h1>
    <NuevoProducto @new="addProduct"></NuevoProducto>
    <h2>Lista de Productos</h2>
    <listaproducto v-for="(producto, index)  of productos"
     :key="index"
     :producto="producto"
      @update="actualizarElemento(index,arguments)"
     @delete="borrarProducto(index)">
    </listaproducto>
  </div>
</template>

<script>
// @ is an alias to /src
import NuevoProducto from '@/components/NuevoProducto.vue';
import listaproducto from '@/components/ListaProducto.vue'
export default {
  name: "Home",
  components:{
    NuevoProducto,
    listaproducto
  },
  data(){
    return {
      productos:[{
        nombre:'Pantalon',
        marca:'levis',
        cantidad:18
      }]
      }
  },
  methods:{
    addProduct(producto){
      this.productos.push(producto);
    },
    borrarProducto(index){
      this.productos.splice(index,1);
    },
    actualizarElemento(index,producto){
    this.producto[index]=producto;
  },
  }
};
</script>
<style lang="css">
h3{
  margin: 40px 0px 40px;
}
</style>