<template>
      <div class="col-md-4 mx-auto ">
     <div class="card rounded-4 bg-success">
        <div class="card-header">
          <h3 class="mb-0 mt-3">Cargar Productos</h3>
        </div>
        <div class="card-body">
          <form class="form" action="" @submit.prevent="NuevoProducto()" >
            <div class="form-group">
              <label for="nombre">Nombre</label>
              <input type="text" class="form-control form-control-lg rounded-4" v-model="nombre" name="nombre" id="nombre">
            </div>
            <div class="form-group">
              <label for="marca">Marca</label>
              <input type="text" class="form-control form-control-lg rounded-4" v-model="marca" name="marca" id="marca">
            </div>
            <div class="form-group">
              <label>Cantidad</label>
              <input type="number" class="form-control form-control-lg rounded-4" v-model="cantidad" name="cantidad" id="cantidad" >
            </div>
            <button type="submit" class="btn btn-outline-primary btn-lg" >Nuevo Producto</button>
          </form>
        </div>
     </div>
  </div>
</template>

<script>
export default {
    name:'NuevoProducto',
    data() {
    return {
        nombre:null,
        marca:null,
        cantidad:null  
    };
  },
  methods:{
      NuevoProducto(){
      const producto={
        nombre:this.nombre,
        marca:this.marca,
        cantidad:this.cantidad             
        }
        if(!this.nombre){
      alert("Debes agregar el nombre!");  		
      }
        if(!this.marca){
      alert("Debes agregar el marca!");  		
      }
        if(!this.cantidad){
      alert("Debes agregar el cantidad!");  		
      } 
      if (this.nombre&&this.marca&&this.cantidad) {
      this.$emit('new',producto);
      this.nombre=null;
      this.marca=null;
      this.cantidad=null;
      }     
  }
}
}
</script>